.over-layer {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  padding: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container-modal {
  width: 410px;
  min-height: max-content;
  background: #0a222d;
  position: relative;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  padding: 41px 55px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.container-label {
  margin: 20px;
}
.btn-modal-close {
  display: flex;
  justify-content: center;
  align-items: center;
  background: none;
  width: 87px;
  height: 38px;
  filter: drop-shadow(0px 1px 1px rgba(0, 0, 0, 0.5));
  border-radius: 8px;
  border: solid 2px #00dfdd;
  cursor: pointer;
}

.btn-modal-close:hover {
  background: #00dfdd;
}

.modal-img {
  height: 100px;
  width: 100px;
  background: rgba(196, 196, 196, 0.5);
  border-radius: 45px;
}

.modal-text {
  margin: 3px 0px 10px;
  padding: 7px;
  color: rgb(31, 30, 30);
  background: #ffffff;
  box-shadow: inset 0px 2px 1px rgba(0, 0, 0, 0.05);
  border-radius: 8px;
}

.modal-link {
  color: rgb(31, 30, 30);
}

.overflow {
  overflow-y: scroll;
  max-height: 154px;
}
